@media ( min-width: 560px ) {
  .text {
    position: absolute;
    width: 100%;
    top: 25%;
    left: 50%;
    transform: translate(-50%,-50%);
  }
  .text p {
    text-align: center;
    font-size: 15px;
    color: #ffffff;
  }
}
.bg {
  width: 100%;
  height: 75vh;
  overflow: hidden;
  margin: 0px auto;
  position: relative;
}
video {
  width: 100%;
}
.text {
  position: absolute;
  width: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
}
